import Layout from '../components/Layout.js'
import withData from '../components/Apollo'
import { graphql } from 'react-apollo'
import gql from 'graphql-tag'

function TeamList({ data: { error, movies }}) {
    return (
        <div>
            {movies.map(movie => (
                <div>
                    <div>
                        <img src={`https://image.tmdb.org/t/p/w200${movie.poster_path}`} />
                    </div>
                    <div>
                        <h2>{movie.title}</h2>
                        <p>{movie.overview}</p>
                    </div>
                </div>
            ))}
            <style jsx>{`
                div > div {
                    display: flex;
                }
            `}</style>
        </div>
    )
  }

const allTeams = gql`{
    movies(query: "Star Wars") {
        id
        title
        overview,
        poster_path
    }
}
`

const Teams = graphql(allTeams, {
    props: ({ data }) => ({
      data,
    })
  })(TeamList)
  
export default withData(() => (
    <Layout>
       <Teams></Teams>
    </Layout>
))